.days{
    display: flex;
    margin: 25px 0 10px;
}
.day{
    width: calc(100%/7);
    text-align: center;
}
.dates{
    display: flex;
    flex-flow: row wrap;
    height: 500px;
    border-top: 2px solid #333333;
    border-right: 2px solid #333333;
}	
.date{
    box-sizing: border-box;
    width: calc(100% / 7);
    text-align: right;
    padding-right: 9px;
    border-left: 2px solid #333333;
    border-bottom: 2px solid #333333;
    cursor:pointer;
}
.date:hover{
    background-color: rgba(71, 255, 194, 0.486);
}
.days :nth-child(7n),
.dates div:nth-child(7n){
    color: blue;
}

.days :nth-child(7n + 1),
.dates div:nth-child(7n + 1) {
  color: red;
}

.other{
    opacity: 0.3;
}
.today{
    position: relative;
    color: #FFFFFF;
}
.today::before{
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: -1;
    display: block;
    width: 30px;
    height: 30px;
    background-color: #FF0000;
    border-radius: 50%;
    transform: translate(-50%, -50%);
    content: '';
}

.main-container{
    width: 70vw;
}
.modal-top{
    border : 3px solid #FFFFFF;
    border-radius: 25px;
    margin-top:15px;
    padding-bottom:26.747px;
}
.modal-context{
    display:flex;
    justify-content: space-around;
    flex-wrap:wrap;
}
.progress-div{
    background-color: rgb(233,233,233);
    border-radius: .5rem;
}
.progress{
    background-color: rgb(71, 255, 194);
    height:10px;
    border-radius: 1rem;
    max-width:60vw;
    transition: 0.5s ease;
    transition-delay: 0.5s;
}
